// Colors
$color-0               : #383838;
$color-1               : #0088ff;
$color-2               : #003d7c;
$color-3               : #12ca64;
$color-4               : #006ae2;
$color-5               : #fb1ca4;
$color-6               : #022e71;
$color-7               : #00003d;
$color-8               : #071130;
$color-9               : #ececec;
$color-10              : #d9d9d9;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.
$font-1                : 'HelveticaNeueLTStd', sans-serif;
$font-2                : 'ITCAvantGardeStd', sans-serif;

$font-family-base      : $font-1;

$font-size-root        : 16px;

$font-size-base        : 1rem; // Assumes the browser default, typically `16px`
$font-size-lg          : 2.5rem;
$font-size-sm          : 1.875rem;
$font-size-xs          : 1.125rem;

$font-size-h1          : 3.75rem; // `60px`
$font-size-h2          : 3.5rem; // `56px`
$font-size-h3          : 2.5rem; // `40px`
$font-size-h4          : 1.875rem; // `30px`
$font-size-h5          : 1.25rem; // `20px`
$font-size-h6          : $font-size-base;

$line-height-base      : 1.35;

$headings-font-weight  : 700;
$headings-line-height  : 1.125;

$hr-border-color       : #ccc;

$brand-primary         : $color-1;
$body-color            : $color-0;
$link-color            : $color-0;
$link-hover-color      : $color-1;
$link-hover-decoration : none;

$container-max-widths  : (
  sm                   : 540px,
  md                   : 720px,
  lg                   : 960px,
  xl                   : 1200px
);
$breakpoints           : $container-max-widths;

$border-radius         : 3px;
$border-radius-sm      : 3px;
$border-radius-lg      : 3px;

$input-border-color    : $color-1;
$input-line-height     : 1.35;
