// SLIDER
.beeketing-slider {
  .nivoSlider:after {
    content: "";
    display: block;
    background-color: rgba($color-6, 0.6);
    background-blend-mode: multiply;
    z-index: 10;

    @include absolute(0, 0, 0, 0);
  }

  .nivoSlider img {
    height: 250px !important;

    @include media-breakpoint-up(md) {
      height: 400px !important;
    }

    @include media-breakpoint-up(lg) {
      height: 560px !important;
    }
  }

  .nivo-caption {
    top: 0;
    bottom: 0;
    z-index: 12;
    padding: 0;
    background-color: transparent;
  }

  .nivo-directionNav {
    width: 100%;
    z-index: 13;

    @include center();
    @include rem(margin-top, -100px);
    @include media-breakpoint-up(md) {
      width: 425px;

      @include rem(margin-top, -10px);
    }

    @include media-breakpoint-up(lg) {
      @include rem(margin-top, 70px);
    }

    a {
      z-index: 12;
      color: $white !important;

      @include rem(font-size, 40px);
    }

    .nivo-prevNav {
      left: 15px;

      @include media-breakpoint-up(lg) {
        left: 0;
      }
    }

    .nivo-nextNav {
      right: 15px;

      @include media-breakpoint-up(lg) {
        right: 0;
      }
    }
  }
}

.beeketing-slider__image {
  display: none;

  @include media-breakpoint-up(xl) {
    display: block;
    text-align: right;

    @include absolute(60px, -32px, 0, 0);
  }
}

.nivoSlider .beeketing-slider__image img {
  position: static;
  max-width: 100% !important;
  height: auto !important;
  max-height: 710px;
}

.beeketing-slider__data {
  display: none;

  @include media-breakpoint-up(lg) {
    display: block;
  }

  .btn {
    @include rem((
      padding-top: 14px,
      padding-bottom: 14px
    ))
  }
}

.beeketing-slider__meta {
  @include media-breakpoint-up(lg) {
    @include rem(font-size, 30px);
  }
}

.beeketing-slider__title {
  @include media-breakpoint-up(lg) {
    @include rem(font-size, 50px);
  }
}

.beeketing-slider__desc {
  @include rem(margin-bottom, 20px);
}

.beeketing-slider__arrow {
  display: none;
  width: 145px;
  height: 110px;
  background: url(../images/arrow-vector-white.png) no-repeat center;
  background-size: 100%;
  transform: translateX(-50%);

  @include absolute(null, null, null, 50%);
  @include rem((
    margin-top: -30px,
    margin-left: 150px
  ));
  @include media-breakpoint-up(xl) {
    display: block;
  }
}

.beeketing-slider__thumbnail {
  width: 100%;
  text-align: center;
  transform: translateX(-50%);

  @include rem((
    font-size: 13px,
    padding-left: 50px,
    padding-right: 50px
  ));
  @include absolute(null, null, 230px, 50%);
  @include media-breakpoint-up(lg) {
    width: auto;
    max-width: 400px;
    padding-left: 0;
    padding-right: 0;
  }

  h2 {
    font-style: italic;
    color: $color-1;

    @include rem(font-size, 18px);
  }
}
