@font-face {
  font-family: 'beeketing';
  src:  url('../fonts/beeketing.eot?f0yt70');
  src:  url('../fonts/beeketing.eot?f0yt70#iefix') format('embedded-opentype'),
    url('../fonts/beeketing.ttf?f0yt70') format('truetype'),
    url('../fonts/beeketing.woff?f0yt70') format('woff'),
    url('../fonts/beeketing.svg?f0yt70#beeketing') format('svg');
  font-weight: normal;
  font-style: normal;
}

.bk {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'beeketing' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.bk-arrows-prev:before {
  content: "\e901";
}
.bk-arrows-next:before {
  content: "\e902";
}
.bk-play-circle-o:before {
  content: "\e900";
}
