// GET STARTED NOW
.beeketing-getstarted {
  position: relative;
  overflow: hidden;

  @include media-breakpoint-up(lg) {
    &:before {
      @include paddingTop(1920, 820);
    }
  }
  @include media-breakpoint-up(xl) {
    &:before {
      @include paddingTop(1920, 1080);
    }
  }
  @include media('>=1440px') {
    &:before {
      @include paddingTop(1920, 900);
    }
  }
  @include media('>=1600px') {
    &:before {
      @include paddingTop(1920, 820);
    }
  }
  @include media('>=1920px') {
    &:before {
      @include paddingTop(1920, 750);
    }
  }
}

.beeketing-getstarted__image {
  display: block;
  font-size: 0;
  background-color: rgba(2, 46, 113, 0.9);
  background-blend-mode: multiply;
  background-size: cover;
  background-attachment: fixed;
  background-position: center bottom;
  background-repeat: no-repeat;

  @include absolute(0, 0, 0, 0);
  @include media-breakpoint-up(xl) {
    background-size: 100%;
  }
}

.beeketing-getstarted__data {
  color: $white;
  z-index: 1;

  @include media-breakpoint-up(lg) {
    @include absolute(0, 0, 0, 0);
  }

  .beeketing-heading {
    @include media-breakpoint-up(lg) {
      @include rem((
        padding-left: 80px,
        padding-right: 80px,
      ))
    }
  }

  .btn-lg {
    @include rem(margin-bottom, 80px);
    @include media-breakpoint-up(lg) {
      @include rem((
        padding: 16px 8px 15px,
        margin-bottom: 0
      ));
    }
  }
}
