// TESTIMONIAL
.beeketing-testimonial {
  @include media-breakpoint-up(lg) {
    @include rem(padding, 0 95px);
  }

  .owl-nav {
    width: 100%;
    transform: translateY(-50%);

    @include absolute(50%, null, null, 0);
    @include rem(margin-top, -75px);
  }

  .owl-prev,
  .owl-next {
    position: absolute;
  }

  .owl-prev {
    left: -65px;
  }

  .owl-next {
    right: -65px;
  }
}

.beeketing-testimonial__title {
  font-weight: 400;
  line-height: 1.5;

  @include rem(margin-bottom, 100px);
  @include media-breakpoint-down(lg) {
    @include rem((
      font-size: 24px,
      margin-bottom: 40px
    ))
  }

  strong {
    font-weight: 500;
  }
}

.beeketing-testimonial__image {
  @include rem(margin-bottom, 30px);
  @include media-breakpoint-up(lg) {
    @include rem(margin-bottom, 85px);
  }

  img {
    display: inline-block !important;
    width: auto !important;
  }
}

.beeketing-testimonial__desc {
  @include rem(margin-bottom, 30px);
  @include media-breakpoint-up(lg) {
    @include rem(font-size, 18px);
  }
}
