// CUSTOMER
.beeketing-customer {
  position: relative;
  z-index: 11;
  background-color: $white;

  @include rem(padding, 30px 0 45px);

  .owl-nav {
    width: 100%;

    @include rem(font-size, 20px);
    @include absolute(50%);
  }

  .owl-prev,
  .owl-next {
    position: absolute;
    transform: translateY(-50%);
  }

  .owl-prev {
    left: 0;
  }

  .owl-next {
    right: -15px;
  }

  .owl-dots {
    @include rem(margin-top, 30px);
  }
}

.beeketing-customer__item {
  display: block;
  font-size: 0;

  @include rem(padding, 0 40px);
  @include media-breakpoint-up(md) {
    @include rem(padding, 0 15px);
  }

  img {
    display: inline-block !important;
    width: auto !important;
  }
}
