a {
  transition: all .3s ease;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

.vhidden {
  transition: all .3s ease;

  @include media-breakpoint-up(lg) {
    @include vhidden();
  }
}

.animated {
  @include media-breakpoint-down(md) {
    animation: unset !important;
  }
}

.sticky {
  position: sticky !important;
  top: 0;

  &:before, &:after {
    content: '';
    display: table;
  }
}

.bk {
  display: inline-block;
  vertical-align: middle;
}

.owl-nav {
  @include rem(font-size, 42px);

  .disabled {
    opacity: 0.2;
  }
}

.owl-dots {
  position: relative;
  text-align: center;
}

.owl-dot {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background-color: $color-10;
  transition: all .3s ease;

  @include rem(margin, 0 6px);

  @include media-breakpoint-up(md) {
    width: 20px;
    height: 20px;
  }

  &.active,
  &:hover {
    background-color: $color-4;
  }

  span {
    display: block;
  }
}

#page {
  position: relative;
}

// Font size & Font weight & Font style
@each $val in 15, 16, 18, 20, 22, 25, 30, 40, 50 {
  .fs-#{$val} {
    font-size: #{($val / 16)}rem !important;
  }
}

@each $val in 300, 400, 500, 600, 700 {
  .fw-#{$val} {
    font-weight: $val;
  }
}

@each $val in italic, normal, oblique {
  .fstyle-#{$val} {
    font-style: $val;
  }
}

// Margins & Paddings
@each $val in 0, 80, -185 {
  @each $breakpoint in md, lg, xs {
    @include media-breakpoint-up($breakpoint) {
      @if $breakpoint == 'md' {
        .mt-md-#{$val} {
          margin-top: #{(($val / 2) / 16)}rem !important;
        }
      } @else if $breakpoint == 'lg' {
        .mt-lg-#{$val} {
          margin-top: #{($val / 16)}rem !important;
        }
      } @else {
        .mt-#{$val} {
          margin-top: #{($val / 16)}rem !important;
        }
      }
    }
  }
}

