(4, $color-4),
(5, $color-5),
(6, $color-6),
(7, $color-7),
(8, $color-8),
(9, $color-9),
(10, $color-10) {
  .bg-#{$name} {
    background-color: $val;
  }

  .color-#{$name} {
    color: $val;
  }
}
