// HEADER
#header {
  position: relative;
  z-index: 99;
  background-color: $white;
  box-shadow: 0px 1px 3px 0px rgba(72, 110, 143, 0.3);
}

.navbar {
  @include rem(padding, 8px 0);
}

.navbar-toggler {
  width: 48px;
  height: 48px;
  border: 2px solid rgba($color-0, 0.3);
  outline: none !important;
  cursor: pointer;
  overflow: hidden;

  @include rem(padding, 0 8px !important);
}

.navbar-toggler-right {
  top: 20px;
  right: 0;
}

.navbar-icon {
  display: block;
  width: 100%;
  height: 2px;
  background-color: rgba($color-0, 0.3);
  transition: all .3s ease;

  @include rem(margin, 8px 0);
}

.navbar-toggler[aria-expanded="true"] {
  .navbar-icon {
    margin-top: 0;
    width: 80%;
    transform: translateY(-50%);

    @include absolute(50%, null, null, 5px);
  }

  .navbar-icon-2 {
    display: none;
  }

  .navbar-icon-1 {
    transform: rotate(45deg);
  }

  .navbar-icon-3 {
    transform: rotate(-45deg);
  }
}

.navbar-toggleable-md .navbar-collapse {
  @include media-breakpoint-up(lg) {
    display: block !important;
  }
}

.navbar-toggleable-md .navbar-nav .nav-link {
  @include rem((
    padding-left: 20px,
    padding-right: 20px
  ))
}

.navbar-nav {
  font-family: $font-2;
  border-top: 1px solid rgba($color-0, 0.2);

  @include rem(margin-top, 8px);

  @include media-breakpoint-up(lg) {
    float: right;
    border-top: none;
    margin-top: 0;
  }
}

.nav-link {
  color: $color-2;

  @include rem((
    padding-bottom: 7px
  ));

  &.btn {
    color: $white;

    @include rem((
      font-size:17px,
      margin-left: 20px
    ));

    @include rem(padding, 6px 5px 6px !important);
  }

  @include media-breakpoint-down(md) {
    @include rem(padding, 10px 0 !important);

    &.btn {
      margin-left:0;
      width: 100%;
    }
  }
}

.get-started {
  display: none;

  @include media-breakpoint-up(sm) {
    display: block;
  }

  @include media-breakpoint-up(lg) {
    display: none;
  }

  &:not(.nav-item) {
    @include absolute(25px, 65px, null);
  }

  &.nav-item {
    display: block;

    @include rem(margin, 10px 0 5px);

    @include media-breakpoint-up(sm) {
      display: none;
      margin: 0;
    }

    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
}
