// FEATURES
.beeketing-features {
  overflow: hidden;

  @include rem(padding-bottom, 55px);
  @include media-breakpoint-up(lg) {
    padding-bottom: 0;
  }

  .beeketing-heading__title {
    @include rem(margin-bottom, 15px);
  }

  .beeketing-heading__meta {
    @include rem(font-size, 15px);
  }
}

.beeketing-features__media {
  text-align: center;
  background-color: $white;

  @include media-breakpoint-up(lg) {
    text-align: left;
    background-color: transparent;
  }
}

.beeketing-features__data {
  @include rem(margin-top, 30px);
  @include media-breakpoint-up(md) {
    margin-top: 0;

    @include vertical-align();
  }
  @include media-breakpoint-up(lg) {
    @include rem(padding-left, 30px);
  }
}

.beeketing-features__arrow {
  display: none;

  @include media-breakpoint-up(lg) {
    display: block;

    @include center(100px, 75px);
  }
  @include media-breakpoint-up(xl) {
    width: 145px;
    height: 110px;
  }
}

.beeketing-features__arrow-icon {
  display: block;
  background: url(../images/arrow-vector.png) no-repeat center;
  background-size: 100%;

  @include absolute(0, 0, 0, 0);
}

.beeketing-features__arrow-icon--white {
  background-image: url(../images/arrow-vector-white.png);
}

// Style 01
.beeketing-features--style-01 {
  .beeketing-features__data {
    @include media-breakpoint-up(lg) {
      @include rem((
        margin-top: -130px,
        padding-right: 30px
      ));
    }
    @include media-breakpoint-up(xl) {
      @include rem(padding-right, 60px);
    }
  }

  .beeketing-features__arrow {
    transform: translateX(-50%);

    @include rem(margin-top, -100px);
  }
}

// Style 02
.beeketing-features--style-02 {
  .beeketing-features__media  {
    background-color: $color-9;

    @include media-breakpoint-up(lg) {
      text-align: right;
      background-color: transparent;

      @include rem(padding-right, 35px);
    }
  }

  .beeketing-features__data {
    @include media-breakpoint-up(lg) {
      @include rem(margin-top, -45px);
    }
  }

  .beeketing-features__arrow {
    transform: translateX(-50%);

    @include rem((
      margin-top: -170px,
      margin-left: 50px
    ));

    @include media-breakpoint-up(xl) {
      @include rem(margin-left, 150px);
    }
  }

  .beeketing-features__arrow-icon {
    transform: scaleX(-1) rotate(50deg);
    filter: FlipH;
    -ms-filter: "FlipH";
  }
}
